:root {
  --black-text: #070707;
  --grey-text: #828282;
  --grey-bg: #F6F6F6;

  --width-desktop: 940px;
  --width-mobile: 376px;
}

html {
  scroll-behavior: auto;
}

body {
  margin: 0;
  overflow-x: hidden;
  font-family: Arial, Helvetica, sans-serif;
}

a {
  text-decoration: none;
}

hr {
  margin: 0;
  border: 1px solid var(--grey-text);
}

p {
  margin: 0;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
}

h2 {
  font-size: 34px;
}

figure {
  margin: 0;
}

button {
  margin: 0;
  padding: 0;
  border: none;
}

.content {
  margin: 0 auto;
}

@media (width >=940px) {
  .content {
    width: var(--width-desktop);
  }

  .none-on-desktop {
    display: none;
  }
}

/* Header */

.header__menu {
  display: flex;
  justify-content: space-between;
  margin: 32px 0;
}

.header__link {
  color: var(--black-text);
  font-weight: bold;
  white-space: nowrap;
}

/* Home section */

.home {
  margin-top: 40px;
  font-weight: bold;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-template-rows: repeat(2, 1fr);
  row-gap: 50px;
  grid-template-rows: auto;
}

.home__name {
  font-size: 47px;
  justify-self: left;
  align-self: center;
}

.home__about-me {
  justify-self: center;
  align-self: end;
}

.home__switch-lang {
  justify-self: right;
  align-self: center;
  writing-mode: vertical-lr;
  transform: rotate(180deg);
}

.home__photo-banner {
  grid-column: 1 / -1;
  overflow: hidden;
}

/* About me section */

.about-me {
  position: relative;
  margin-top: 104px;
  background-color: var(--grey-bg);
  height: 500px;
}

.about-me::before {
  content: '';
  position: absolute;
  left: 0;
  top: 0;
  height: 100%;
  width: calc((100vw - var(--width-desktop)) / 2);
  max-width: 100%;
  background-color: var(--grey-bg);
  transform: translateX(-100%);
}

.about-me::after {
  content: '';
  position: absolute;
  right: 0;
  top: 0;
  height: 100%;
  width: calc((100vw - var(--width-desktop)) / 2);
  max-width: 100%;
  background-color: var(--grey-bg);
  transform: translateX(100%);
}

.about-me__title {
  text-align: center;
  padding: 120px 0 18px;
}

.about-me__paragraphs {
  display: grid;
  justify-items: center;
  row-gap: 50px;
}

.about-me__paragraph {
  font-size: 18px;
  text-align: center;
  font-weight: 700;
}

/* Skills section */

.skills {
  margin-top: 90px;
  display: grid;
  justify-items: center;
  row-gap: 68px;
}

.skills__subtitle {
  margin-bottom: 15px;
}

.skills__apps {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  justify-items: center;
  width: 100%;
}

.skills__app-card {
  display: grid;
  justify-items: center;
}

.slills__app-card-description {
  margin: 24px 0 40px;
  font-weight: 700;
  font-size: 14px;
  color: var(--grey-text);
  text-align: center;
}

/* Portfolio section */

.portfolio {
  position: relative;
  margin-top: 110px;
  background-color: var(--grey-bg);
}

.portfolio::before {
  content: '';
  position: absolute;
  left: 0;
  top: 0;
  height: 100%;
  width: calc((100vw - var(--width-desktop)) / 2);
  max-width: 100%;
  background-color: var(--grey-bg);
  transform: translateX(-100%);
}

.portfolio::after {
  content: '';
  position: absolute;
  right: 0;
  top: 0;
  height: 100%;
  width: calc((100vw - var(--width-desktop)) / 2);
  max-width: 100%;
  background-color: var(--grey-bg);
  transform: translateX(100%);
}

.portfolio__title {
  text-align: center;
  padding: 112px 0 47px;
}

.portfolio__item {
  margin: 0 auto;
  padding-top: 100px;
}

.portfolio__item figcaption {
  text-align: center;
  font-weight: 700;
  font-size: 18px;
  text-decoration: underline;
  padding-top: 45px;
}

.portfolio__item:last-child {
  padding-bottom: 96px;
}

/* Footer / Contacts */

.contacts {
  margin-top: 114px;
  text-align: center;
}

.contacts__title {
  text-align: center;
}

.contacts__subtitle {
  margin: 20px 0 29px;
  text-align: center;
}

.contacts__send-messege {
  margin: 0 auto;
  width: 180px;
  height: 42px;
  background-color: #000;
  border-radius: 2em;
}

.contacts__send-messege a {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
  color: #fff;
  font-size: 18px;
}

.contacts__socials {
  margin: 0 auto;
  width: 342px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  text-align: center;
}

.contacts__socials-item {
  margin: 85px 0 45px;
  transform: translateY(-50%);
}

.contacts__socials-description {
  margin-bottom: 120px;
}


@media (width <=376px) {
  .content {
    width: calc(var(--width-mobile) - 30px * 2);
  }

  .none {
    display: none;
  }

  /* Header mobile */

  .header-mobile {
    height: 87px;
    width: 100vw;
  }

  .header-mobile-btn {
    position: absolute;
    top: 68px;
    right: 0;
    display: inline-block;
    height: 40px;
    width: 53px;
    padding: 10px;
    box-sizing: border-box;
  }

  .header-mobile-btn-top,
  .header-mobile-btn-middle,
  .header-mobile-btn-bottom {
    position: relative;
    top: 0;
    width: 33px;
    height: 4px;
    background-color: black;
    transition: all .2s;
  }

  .header-mobile-btn-middle {
    margin: 4px 0;
  }

  .top-rotate {
    transform: rotate(45deg);
    top: 8px;
    background-color: var(--grey-text);
  }

  .scale {
    transform: scale(0);
    background-color: var(--grey-text);
  }

  .bottom-rotate {
    transform: rotate(-45deg);
    top: -8px;
    background-color: var(--grey-text);
  }


  .header__link {
    color: var(--grey-text);
    font-weight: bold;
    font-size: 34px;
  }

  .header__menu {
    position: absolute;
    left: -50%;
    flex-direction: column;
    justify-content: flex-start;
    row-gap: 50px;
    transition: all .2s;
    height: 100%;
    margin: 0;
    padding: 32px 0;
    background-color: whitesmoke;
  }

  .left-to-right-menu {
    left: 30px;
    width: 100%;
  }

  .header__link:first-child {
    color: var(--black-text);
  }

  /* Home section mobile */

  .home {
    grid-template-columns: 1fr;
    margin-top: -10px;
  }

  .home__about-me {
    align-self: center;
    justify-self: left;
  }

  .home__switch-lang {
    position: absolute;
  }

  /* About me section mobile */

  .about-me {
    margin-top: 83px;
  }

  .about-me__title {
    padding-top: 81px;
  }

  .about-me::before {
    width: calc((100vw - (var(--width-mobile) - 30px * 2)) / 2);
  }

  .about-me::after {
    width: calc((100vw - (var(--width-mobile) - 30px * 2)) / 2);
  }

  /* Skills section ,obile */

  .skills {
    margin-top: 83px;
    row-gap: 50px;

  }

  .skills__subtitle {
    align-self: center;
    margin-bottom: 20px;
  }

  .skills__apps {
    grid-template-rows: repeat(4, 1fr);
    grid-template-columns: 1fr;
    row-gap: 80px;
  }

  /* Portfolio section mobile */

  .portfolio {
    margin-top: 98px;
  }

  .portfolio::before {
    width: calc((100vw - (var(--width-mobile) - 30px * 2)) / 2);
  }

  .portfolio::after {
    width: calc((100vw - (var(--width-mobile) - 30px * 2)) / 2);
  }

  .portfolio__title {
    padding: 81px 0 13px;
  }

  .portfolio__item {
    padding-top: 78px;
  }

  img {
    max-width: calc(100vw - 60px);
  }

  .portfolio__item figcaption {
    padding-top: 33px;
  }

  .portfolio__item:last-child {
    padding-bottom: 60px;
  }

  /* Footer mobile / Contacts mobile */

  .contacts {
    margin-top: 82px;
  }

  .contacts__subtitle {
    margin: 26px 0 24px;
  }

  .contacts__socials {
    width: 100%;
  }

  /* FIXME Поправить высоту height */

  .contacts__socials-description {
    margin-bottom: 90px;
  }

  /* TODO Поменять шрифты */
  /* TODO Сделать меню mobile */
}

@media (width <=320px) {

  .home__name,
  .home__about-me {
    padding-left: 10px;
  }

  .home__photo-banner {
    justify-self: center;
  }
}
